.header {
    position: absolute;
    width: 100%;
    z-index: 1;
    left: 0;
    top: 35px;

    &__row {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 85px;
    }

    &__logo {
        // max-height: 1000%;
        // max-width: 1000%;
        position: absolute;
        left: 50%;
        top: -10;
        transform: translate(-50%);
        height: 70px;
        // z-index: 1;
    }

    @media (max-width: 770px) {
        top: 15px;
    }
}

.menu {
    
    &__list {
        display: flex;

        li {
            position: relative;
            padding: 0 40px;

            &:nth-child(2) {
                padding: 0 80px 0 40px;
                &::after {
                    display: none
                }
            };

            &:nth-child(3) {
                padding: 0 40px 0 80px;
            };

            &:last-child {
                &::after {
                    display: none;
                }
            }

            &:after {
                content: '✪';
                position: absolute;
                right: 0;
                top: 20%;
            }

        }
    }

    &__link {
        color: black;
        text-transform: uppercase;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 3px;
        z-index: 3;
    }

    @media (max-width: 770px) {
        &__body {
            position: fixed;
            width: 100%;
            height: 100%;
            top: -100%;
            left: 0;
            background-color: rgb(255, 94, 161);
            padding-top: 135px;
            overflow: auto;
            transition: all 0.3s ease 0s;
            
            &.active {
                top: 0;
                transition: all 0.3s ease 0s;
            }
        }

        &__link {
            color: white;
            font-size: 35px;
        }

        &__list {
            position: absolute;
            left: 50%;
            transform: translate(-50%);
            z-index: 500;
            flex-direction: column;
            justify-content: center;
            align-items: center;
    

            li {
                padding: 0 0 35px 0 !important;

                &::after {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 767px) {
    .header__logo {
        position: absolute;
    }
}

.icon-menu {

    @media (max-width: 767px) {
        display: block;
        position: fixed;
        top: 18px;
        right: 10px;
        width: 30px;
        height: 18px;
        cursor: pointer;
        z-index: 50;
        
        span {
            transition: all 0.3s ease 0s;
            top: 8px;
            left: 0px;
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: #000;
    
            &:first-child {
                top: 0;
            }
    
            &:last-child {
                bottom: 0;
                top: auto;
            }
        }
    
        &.active {
    
            span {
                transform: scale(0);
                background-color: #fff;
    
                &:first-child {
                    transform: rotate(-45deg);
                    top: 8px;
                }
    
                &:last-child {
                    transform: rotate(45deg);
                    bottom: 8px;
                }
            }
        }
    }
}

body.lock {
    overflow: hidden;
}
